import { useState, useEffect, useCallback } from "react";
import {
  getAllProductTypes,
  getProductType,
  getProductTypes,
} from "../api/categoryApis";

const useProductType = (subcategoryId, productTypeId) => {
  const [allProductTypes, setAllProductTypes] = useState([]);
  const [productTypes, setProductTypes] = useState([]);
  const [productType, setProductType] = useState(null);
  const [loading, setLoading] = useState(false);
  const [error, setError] = useState(null);

  const fetchAllProductTypes = useCallback(async () => {
    setLoading(true);
    setError(null);
    try {
      const cacheKey = "productTypes";
      const cachedData = sessionStorage.getItem(cacheKey);
      if (cachedData) {
        setAllProductTypes(JSON.parse(cachedData));
      } else {
        const response = await getAllProductTypes();

        if (response?.status) {
          console.log(response.data);
          sessionStorage.setItem(
            cacheKey,
            JSON.stringify(response.data.productTypeList)
          );
          setAllProductTypes(response.data.productTypeList);
        }
      }
    } catch (error) {
      console.error(error);
      setError(error.message);
    } finally {
      setLoading(false);
    }
  }, []);

  const fetchProductTypesBySubcategory = useCallback(async () => {
    if (!subcategoryId) return;
    setLoading(true);
    setError(null);
    try {
      const cacheKey = `productTypes_${subcategoryId}`;
      const cachedData = sessionStorage.getItem(cacheKey);
      if (cachedData) {
        setProductTypes(JSON.parse(cachedData));
      } else {
        const response = await getProductTypes(subcategoryId);

        if (response?.status) {
          console.log(response.data);
          sessionStorage.setItem(
            cacheKey,
            JSON.stringify(response.data.productTypeList)
          );
          setProductTypes(response.data.productTypeList);
        }
      }
    } catch (error) {
      console.error(error);
      setError(error.message);
    } finally {
      setLoading(false);
    }
  }, [subcategoryId]);

  const fetchProductType = useCallback(async () => {
    if (!productTypeId) return;
    setLoading(true);
    setError(null);
    try {
      const cacheKey = `productType_${productTypeId}`;
      const cachedData = sessionStorage.getItem(cacheKey);
      if (cachedData) {
        setProductType(JSON.parse(cachedData));
      } else {
        const response = await getProductType(productTypeId);

        if (response?.status) {
          console.log(response.data);
          sessionStorage.setItem(
            cacheKey,
            JSON.stringify(response.data.productTypeDetails)
          );
          setProductType(response.data.productTypeDetails);
        }
      }
    } catch (error) {
      console.error(error);
      setError(error.message);
    } finally {
      setLoading(false);
    }
  }, [productTypeId]);

  useEffect(() => {
    fetchAllProductTypes();
  }, [fetchAllProductTypes]);

  useEffect(() => {
    fetchProductTypesBySubcategory();
  }, [fetchProductTypesBySubcategory]);

  useEffect(() => {
    fetchProductType();
  }, [fetchProductType]);

  return {
    allProductTypes,
    productTypes,
    productType,
    loading,
    error,
    refresh: fetchAllProductTypes,
  };
};

export default useProductType;
